.TodoItem {
	display: flex;
	align-items: center;
	justify-content: space-around;
	border: 1px solid #ccc;
	width: 700px;
	overflow-wrap: break-word;
	background-color: white;
}

.TodoItem p {
	margin-left: 50px;
	width: 500px;
}

.TodoItem button {
	background-color: transparent;
	border: none;
	cursor: pointer;
}
